@import "../../App.scss";

.visible--beyond-mobile {
    display: block;
}

.visible--beyond-mobile {
    display: none;
}

.title {
    text-align: center;
    font-weight: 600;
    font-size: 1.125rem;
    color: $lapis_medium_light;
}

.date_location {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;

    p {
        margin: .125rem 0;
        color: $black_light;
        font-weight: 600;
        width: 100%;
    }
}

.faint, .faint a {
    color: $black_light;
    padding: 0;
    margin: 0;
    font-weight: 600;
}

.program_header {
    color: $black;
    font-weight: 600;
    width: fit-content;
    margin: .5rem 0;
    padding: 0;
}

.program_tabs {
    display: flex;
    justify-content: space-around;
    margin: 1rem 0;
    border-bottom: .125px solid $orange_web_light;

    p {
        font-weight: 500;
        font-size: 1.125rem;
        width: 33.3%;
        text-align: center;
        padding: .25rem 0;
        margin: 0;
        color: $yale_blue_light;

        span {
            font-size: 1.125rem;
        }
    }

    .active {
        border-bottom: 2px solid $orange_web;
        color: $yale_blue;
    }

    p:hover {
        cursor: pointer;
    }
}

tr, td {
    text-align: left;  
}

.time {
    display: none;
}

.program_description {
    width: 80%;
    border-bottom: 1px solid $black_light;
    padding: 1rem 0;


    h4 {
        font-weight: 600;
        font-size: 1.125rem;
        margin: .5rem 0;
    }

    .program_details {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        margin: .5rem 0;

        span {
            margin-top: 0;
        }

        a {
            color: $black_light;
        }
    }
}

tr, td {
    padding: 1rem;
}

@media(min-width: 748px) {

    .visible--beyond-mobile {
        display: inline;
    }

    .visible--only-mobile {
        display: none;
    }

    .time {
        display: table-cell;
        width: 20%;
        border-bottom: 1px solid $black_light;
        border-right: 1px solid $black_light;
    }

    .date_location {
    
        p {
            width: 50%;
            text-align: right;
        }
    }

    .program_description {
        padding: 1rem;
    }
}
