@import url("https://fonts.googleapis.com/css2?family=DotGothic16&family=Roboto+Condensed&display=swap");

$pine_tree: rgb(31, 39, 27);
$pine_tree_light: rgba(31, 39, 27, 0.5);
$blue_sapphire: rgb(25, 100, 126);
$blue_sapphire_light: rgba(25, 100, 126, 0.6);
$medium_turquoise: rgb(80, 216, 215);
$medium_turquoise_light: rgba(80, 216, 215, 0.5);
$snow: rgb(255, 251, 254);
$snow_light: rgba(255, 251, 254, 0.1);
$sandy_brown: rgb(238, 150, 75);
$sandy_brown_light: rgba(238, 150, 75, 0.5);
$lapis: rgb(30, 97, 148);
$lapis_light: rgba(30, 97, 148, 0.3);
$lapis_small_light: rgba(30, 97, 148, 0.6);
$lapis_medium_light: rgba(30, 97, 148, 0.8);
$black: rgb(0, 0, 0);
$black_light: rgba(0, 0, 0, 0.5);
$ghost_white: rgb(244, 244, 249);
$yellow_orange: rgb(255, 167, 51);
$yellow_orange_light: rgba(255, 167, 51, 0.5);
$prussion_blue: rgb(16, 56, 86);
$prussion_blue_light: rgba(16, 56, 86, 0.5);
$honey_yellow: rgb(246, 174, 45);
$honey_yellow_light: rgba(246, 174, 45, 0.5);
$yale_blue: rgb(23, 78, 120);
$yale_blue_light: rgba(23, 78, 120, 0.5);
$yale_blue_faint: rgba(23, 78, 120, 0.7);
$orange_web: rgb(252, 163, 17);
$orange_web_light: rgba(252, 163, 17, 0.5);

@keyframes pop-in {
  0% {
    opacity: 0;
    transform: translateY(-4rem) scale(0.8);
  }
  100% {
    opacity: 1;
    transform: none;
  }
}

@keyframes fadeIn {
  0% {
    opacity:0;
  }
  100% {
    opacity:1;
  }
}

@-moz-keyframes fadeIn {
  0% {
    opacity:0;
  }
  100% {
    opacity:1;
  }
}

@-moz-keyframes fadeIn {
  0% {
    opacity:0;
  }
  100% {
    opacity:1;
  }
}

.App {
  font-family: "Roboto Condensed", sans-serif;
  position: relative;
  background-color: $ghost_white;
}

.text {
  padding: 1rem;
  font-size: 18px;
  font-family: "Roboto Condensed", sans-serif;

  p {
    line-height: 2rem;
  }
}

.scroll_up {
  position: fixed;
  z-index: 1;
  right: 0;
  text-align: right;
  bottom: 10%;
  margin: 0;
  padding: 0;
  animation: fadeIn ease 3s;
  -webkit-animation: fadeIn ease 3s;
  -moz-animation: fadeIn ease 3s;

  i {
    color: $ghost_white;
    background-color: $yale_blue_faint;
    padding: .75rem;
    font-size: 1.5rem;
  }

  i:hover {
    cursor: pointer;
  }
}

.newSection {
  padding-top: 3.75rem;
  padding-bottom: 3.75rem;
}

#overview {
  background-color: $black;
  color: $ghost_white;
}

#people {
  background-color: $black;
}

@media (min-width: 748px) {
  #overview .text {
    padding: 1.5rem 4rem;

    h2 {
      padding-bottom: 0;
    }
  }

  .text {
    padding: 0 4rem;
  }
}
